<!--  -->
<template>
  <div class="box">
    <div class="body">
      <h1 id="logo"></h1>
      <el-row :span="24" class="content">
        <el-col :span="12" class="first">
          <div class="bigImg"></div>
        </el-col>
        <el-col :span="12" class="last">
          <h2 class="title">礼品淘分站管理后台系统</h2>
          <div class="login">
            <div class="userName">
              <span>用户名：</span>
              <el-input style="width:200px" placeholder="请输入内容" v-model="userName" clearable></el-input>
            </div>
            <div class="pwd">
              <span class="text">密 &nbsp;&nbsp;码：</span>
              <el-input style="width:200px" placeholder="请输入密码" v-model="pwd" show-password></el-input>
            </div>
            <div class="btnLogin">
              <el-button @click="handleLogin">登录</el-button>
            </div>
          </div>
        </el-col>
      </el-row>
      <div class="footer"></div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      userName: "",
      pwd: "",
      resData:""
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    handleLogin() {
      console.log("1111");

      //判断 账号密码
      let { userName, pwd } = this;

      // let data = {
      //   account: "admin",
      //   password: this.$md5("123456")
      // };
      this.$axios({
        headers: {
          deviceCode: "A95ZEF1-47B5-AC90BF3",
          domain: "taobao.tianyalei.com/xxxx"
        },
        method: "post",
        url: "/api/sysUser/login",
        // url: "http://taobao.tianyalei.com/giftmall/sysUser/login",
        data: this.$Qs.stringify({account:userName,password:this.$md5("123456")})
      })
        .then(res => {

          let {resData} = this
          console.log(res);
          // 当用户不存在时
          if (res.data.code === 10001 || res.data.code === 10002) {
            this.$message({
              message: "用户名或密码错误",
              type: "warning"
            });
          } else if (res.data.code === 200) {
            this.$message({
              message: "登录成功",
              type: "success"
            });
            
            //跳转路由
            this.$router.push({ path: "/WorkPreview" });

            // 存一下res中的data
            resData = res.data.data  
            
            console.log(resData)

            //dispath 触发vuex actions
            // this.$store.dispatch('login',resData)

            // 
            window.localStorage.setItem('token',resData)
          }

          // 当用户存在时
        })
        .catch(err => {
          console.log(err);
        })
        

      // this.$router.push({ path: "/WorkPreview" });
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped lang="stylus">
.el-container .el-main {
  position: absolute;
  top: -60px;
  left: -200px !important;
}

.box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: hidden;
}

.body {
  pading: 0;
  box-sizing: border-box;
  height: 887px;
  background: #fff;

  // display: flex;//flex布局
  // justify-content: center;//使子项目水平居中
  // align-items: center;//使子项目垂直居中
  // padding-top:200px
  #logo {
    height: 100px;
    background: #fff;
    background-image: url('./../../../static/image/logo.png');
    background-repeat: no-repeat;
    background-position: 80px 30px;
  }

  .first {
    height: 600px;
    position: relative;

    .bigImg {
      position: absolute;
      right: -40px;
      top: 40px;
      width: 535px;
      height: 533px;
      background: url('./../../../static/image/big.png');
    }
  }

  .content {
    background: #eee;
  }

  .last {
    box-sizing: border-box;
    padding-top: 35px;
    width: 400px;
    height: 300px;
    background: #fff;
    position: relative;
    top: 100px;
    left: 100px;

    .title {
      text-align: center;
    }

    .login {
      margin-top: 30px;

      .userName, .pwd, .btnLogin {
        text-align: center;
      }

      .pwd {
        margin-top: 15px;

        .text {
          margin-right: 2px;
        }
      }

      .btnLogin {
        margin-top: 15px;
      }
    }
  }

  .bigImg {
  }
}
</style>


