<!--  -->
<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>业绩统计</el-breadcrumb-item>
            <el-breadcrumb-item>营收日报</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="inputWrap">
            <el-col :span="16">
              <span >日期</span>&nbsp;&nbsp;&nbsp;
              <el-date-picker
                v-model="value1"
                type="daterange"
                value-format="yyyy-MM-dd"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              ></el-date-picker>&nbsp;&nbsp;
              <el-button>搜索</el-button>
            </el-col>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="name" label="日期" width="120"></el-table-column>
      <el-table-column prop="name" label="总订单数" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="总包裹数" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="新增注册" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="下单用户" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="总用户数" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="分站奖金" width="120" align="center"></el-table-column>
      <el-table-column prop="name" label="分站累计奖金" width="120" align="center"></el-table-column>
      <el-table-column  ></el-table-column>

    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",
      value2: "",
      formInline: {
        user: "",
        region: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        ,
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        ,
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        ,
        {
          date: "2016-05-02",
          name: "王小虎"
        }
      ]
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang='stylus' scoped>
.el-col-24 {
  margin-bottom: 20px;

  .title {
    height: 40px;
    line-height: 40px;
    border: solid 1px #ccc;
    border-bottom:0px
    background: #eee;
  }

  .wrap {
    border: 1px solid #ccc;
    background: #fff;

    .inputWrap {
      overflow: hidden;
      padding-top: 15px;
    }
  }

  .el-col-16 {
    padding-left: 35px;
    height: 60px;
  }

  .btn>.el-form {
    padding-left: 35px;
  }
}

  .el-breadcrumb {
    height: 40px;
    line-height: 40px;
    margin-left: 35px;
  }


  .el-table{
    border:1px solid #ccc
  }
</style>


