<template>
  <div class="page">
    <el-col :span="24">
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="rgb(81, 78, 78)"
        text-color="#fff"
        active-text-color="#ffd04b"
        @select="handleSelect"
      >
        <el-submenu index="1">
          <template slot="title">
            <span>业务概览</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="1-1" >
              <span @click="goEmbody">体现</span>
            </el-menu-item>
            <el-menu-item index="1-2" @click="goRunning">流水</el-menu-item>
            
          </el-menu-item-group>
        </el-submenu>

        <el-submenu index="2">
          <template slot="title">
            <span>用户管理</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="2-1" >
              <span @click="goEmbody">用户列表</span>
            </el-menu-item>
            <el-menu-item index="2-2">任务列表</el-menu-item>
             <el-menu-item index="2-3">包裹列表</el-menu-item>
             <el-menu-item index="2-4">商家提现</el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <el-submenu index="3">
          <template slot="title">
            <span>站点管理</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="3-1" >
              <span @click="goEmbody">定价策略</span>
            </el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <el-submenu index="4">
          <template slot="title">
            <span>业绩统计</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="4-1" >
              <span @click="goEmbody">营收日报</span>
            </el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        
      </el-menu>
    </el-col>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {};
  },
  methods: {
    goTaskList (){  //去任务列表
      this.$router.push({path:'/TaskList'})
    },
    goRunning () { //去流水
      this.$router.push({path:'/WorkPreview/Running'})
    },
    goEmbody () { 
      console.log('111')
      
      console.log('222')
    },
    // 点击每个不同的菜单进行跳转路由的函数
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      if(keyPath[1]==="1-1"){
        console.log('去提现')
        this.$router.push({path:'/WorkPreview/Embody'})
      }else if(keyPath[1]==="1-2"){
        this.$router.push({path:'/WorkPreview/Running'})
      }else if(keyPath[1]==="2-1"){
        this.$router.push({path:'/Users'})
      }else if(keyPath[1]==="2-2"){
        this.$router.push({path:'/TaskList'})
      }else if(keyPath[1]==="2-3"){
        this.$router.push({path:'/PackgeList'})
      }else if(keyPath[1]==="2-4"){
        this.$router.push({path:'/MerchantList'})
      }else if(keyPath[1]==="3-1"){
        this.$router.push({path:'/PricingStrategy'})
      }else if(keyPath[1]==="4-1"){
        this.$router.push({path:'/EaringsRecord'})
      }

      
    },
    // 菜单激活时的回调
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
      //
      if(keyPath[0]==="1"){
        //  query 的形式  进行路由跳转 也可以传参
        this.$router.push({path:'/WorkPreview'})
        
      }
      
      

    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  components: {}
};
</script>

<style scoped >
  span {
    display: block;
    width:100%;
    height:100%;
  }
</style>




