<!--  -->
<template>
  <div class>
    <el-row>
      <el-col :span="24" class="top">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>流水账单</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="count">账户余额: 0</div>
          <div class="time">
            <span style="margin-right:15px;font-size:14px;color:#ccc;margin-left:5px">日期</span>
            <el-date-picker
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
            <el-button style="width:100px">搜索</el-button>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="date" label="日期"  align="center"></el-table-column>
      <el-table-column prop="name" label="款项" :formatter="formatRole"  align="center"></el-table-column>
      <el-table-column prop="address" label="金额" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",
      value2: "",
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ]
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    cellStyle() {
      return "text-align:center";
    },
    rowClass() {
      return "text-align:center";
    },
    formatRole(row, column) {
      let name = row[column.property];
      let n = "你好" + name;
      return n;
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='stylus' scoped>
.top {
  width: 100%;
  height: 150px;
  background: pink;

  .title {
    height: 35px;
    line-height: 35px;
    background: #eee;
    border: 1px solid #ccc;
    border-bottom: 0px;
  }

  .wrap {
    background: #fff;
    border: 1px solid #ccc;
    height: 115px;

    .count, .time {
      margin-top: 15px;
      padding-left: 20px;
    }
  }
}

.el-table {
  border: 1px solid #ccc;
  margin-top: 15px;
}
.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}

</style>


