<!--  -->
<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>商家提现</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="inputWrap">
            <el-col :span="8">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="状态" class="state">
                  <el-select v-model="formInline.region" placeholder="全部" style="width:250px">
                    <el-option label="区域一" value="shanghai"></el-option>
                    <el-option label="区域二" value="beijing"></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="16">
              <span>申请时间</span>&nbsp;&nbsp;&nbsp;
              <el-date-picker
                v-model="value1"
                type="daterange"
                value-format="yyyy-MM-dd"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              ></el-date-picker>&nbsp;&nbsp;
              <el-button size="medium" style="width:100px">搜索</el-button>
            </el-col>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="name" label="商家名称" align="center"></el-table-column>
      <el-table-column prop="name" label="申请时间" align="center"></el-table-column>
      <el-table-column prop="name" label="提现金额" align="center"></el-table-column>
      <el-table-column prop="name" label="状态" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",
      value2: "",
      formInline: {
        user: "",
        region: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        }
      ]
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang='stylus' scoped>
.el-col-24 {
  margin-bottom: 20px;

  .title {
    height: 40px;
    line-height: 40px;
    border: solid 1px #ccc;
    border-bottom:0px
    background: #eee;
  }

  .wrap {
    border: 1px solid #ccc;
    background: #fff;

    .inputWrap {
      overflow: hidden;
      padding-top: 15px;
    }
  }

  .el-col-8 {
    padding-left: 35px;
  }

  .btn>.el-form {
    padding-left: 35px;
  }
}
.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}

.el-table{
  border:1px solid #ccc
}
</style>


