<template>
  <div class="page">
    <el-col :span="24">
      <div class="title">礼品淘-分站后台管理系统</div>
      <div class="user">
        <div>
          <div href="#">
            <i class="iconfont icon-LC_icon_user_fill_3"></i>
          </div>
        </div>
        <div>
          <div href="#" style="cursor:pointer;">admin</div>
        </div>
        <div>|</div>
        <div>
          <div href="#" style="cursor:pointer;" >修改密码</div>
        </div>
        <div>
          <div href="#" style="cursor:pointer;" @click="exit">退出登录</div>
        </div>
      </div>
    </el-col>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {};
  },
  components: {},
  methods: {
    //退出登录
    exit() {
      /*
        逻辑:  当点击退出登录时  判断 有没有token 
               如果有 弹出提示框问他是否退出  点是  删除token 跳回登录页面,如果没有token 点击不做反应
      
      */

      
      if (window.localStorage.getItem("token")) {
        this.$confirm("您确定要退出登录吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          // type: 'warning',
          center: true
        })
          .then(() => {
            window.localStorage.removeItem("token");
            this.$router.push({ path: "/Login" });
          })
          .catch(() => {});
      } else {
        return;
      }
    }
  }
};
</script>

<style scoped lang='stylus'>
.page {
  width: 100%;
  background: #ec3440;
  height: 60px;
}

.el-col {
  position: relative;
}

.el-col>div {
  float: left;

  &.title {
    position: absolute;
    left: 20px;
    color: #fff;
    line-height: 60px;
  }
}

.el-col .user {
  position: absolute;
  right: 20px;

  &>div {
    float: left;
    color: #fff;
    line-height: 60px;
    margin-left: 10px;
  }
}

a {
  text-decoration: none;
  color: #fff;
}
</style>



