<!--  -->
<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>任务列表</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="inputWrap">
            <el-col :span="10">
              <span>支付时间</span>&nbsp;&nbsp;&nbsp;
              <el-date-picker
                v-model="value1"
                type="daterange"
                range-separator="至"
                value-format="yyyy-MM-dd"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              ></el-date-picker>
            </el-col>
            <el-col :span="7">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="用户账号">
                  <el-input v-model="formInline.account" placeholder="审批人" align="center"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="7">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="平台订单号">
                  <el-input v-model="formInline.orderNum" placeholder="审批人" align="center"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </div>
          <div class="btn">
            <el-button style="width:100px" @click="search">搜索</el-button>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="name" label="平台任务号" align="center"></el-table-column>
      <el-table-column prop="name" label="平台" align="center"></el-table-column>
      <el-table-column prop="name" label="方式" align="center"></el-table-column>
      <el-table-column prop="name" label="用户账户号" align="center"></el-table-column>
      <el-table-column prop="name" label="任务金额" align="center"></el-table-column>
      <el-table-column prop="name" label="包裹数量" align="center"></el-table-column>
      <el-table-column prop="name" label="支付金额" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",  //  点击日期选择器   选择的日志 开始时间和结束时间会在
      formInline: {
        account: "",
        orderNum: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        }
      ]
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    search () {

      console.log(this.value1)
      console.log(this.value2)

      let {account,orderNum,value1} = this
      let token = window.localStorage.getItem('token')
      this.$axios({
        method:'get',
        url:'/api/task',
        headers:{
          Authorization:token,
          deviceCode: "A95ZEF1-47B5-AC90BF3",
          domain: "taobao.tianyalei.com/xxxx",
          "Content-type":"application/x-www-form-urlencoded; charset=utf-8"
        },
        data:this.$Qs.stringify({
          page:0,         //页数
          size:10,         //大小
          begin:'2019-08-09',  //开始时间
          end:value1,         //结束时间
          account:'admin',   //用户账号        应该要改 不能写死 
          webSiteld:'',  //分站id
          orderNum,  //订单编号
        })
      })
    },
    getSTime (val) {
      this.value1 = val
    }
  }
};
</script>
<style lang='stylus' scoped>

.el-col-24 {
  margin-bottom: 20px;

  .title {
    height: 40px;
    line-height: 40px;
    border: solid 1px #ccc;
    border-bottom:0px
    background: #eee;
  }

  .wrap {
    border: 1px solid #ccc;
    background: #fff;
    height 130px
    .inputWrap {
      overflow: hidden;
      padding-top: 15px;
    }
  }

  .el-col-10 {
    padding-left: 35px;
  }
  .btn{
    text-align center
  }
  
}

.el-date-editor {
  width: 75%;
}

.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}

.el-table{
  border:1px solid #ccc
}
</style>


