import Vue from 'vue'
import Router from 'vue-router'
import WorkPreview from '@/components/WorkPreview/WorkPreview'  //业务概览
import Login from '@/components/Login/Login'   // 登录页面
import Embody from '@/components/WorkPreview/Embody/Embody' //业务概览下 体现
import Running from '@/components/WorkPreview/Running/Running' //业务概览下 流水
import Users from '@/components/Users/Users' //业务概览下 用户列表

import MerchantList from '@/components/MerchantList/MerchantList' //商家列表
import PackgeList from '@/components/PackgeList/PackgeList' //包裹列表
import PricingStrategy from '@/components/PricingStrategy/PricingStrategy' //定价策略
import TaskList from '@/components/TaskList/TaskList' //任务列表
import EarningsRecord from '@/components/EarningsRecord/EarningsRecord' //营收日报
Vue.use(Router)

export default new Router({
  mode : 'history',
  routes:[
    { //默认重定向显示路由   
      path:'/',
      name:'Login',
      redirect:'/Login'
    },
    { //登录
      path:'/login',
      name:'Login',
      component:Login,
      
    },
    { //登录
      path:'/login',
      name:'Login',
      component:Login,
      
    },
    {//业务概览
      path:'/WorkPreview',
      name:'WorkPreview',
      component:WorkPreview,
      meta: {
        showSlideBar: true
      },
    },
    { // 业务概览下 提现
      path:'/WorkPreview/Embody',
      name:'Embody',
      component:Embody,
      meta: {
        showSlideBar: true
      },
    },
    { //业务概览下 流水
      path:'/WorkPreview/Running',
      name:'Running',
      component:Running,
      meta: {
        showSlideBar: true
      },
    },
    { // 用户列表
      path:'/Users',
      name:'Users',
      component:Users,
      meta: {
        showSlideBar: true
      },
    },
    { //商家列表
      path:'/MerchantList',
      name:'MerchantList',
      component:MerchantList,
      meta: {
        showSlideBar: true
      },
    },
    { //包裹列表
      path:'/PackgeList',
      name:'PackgeList',
      component:PackgeList,
      meta: {
        showSlideBar: true
      },
    },
    { //定价策略
      path:'/PricingStrategy',
      name:'PricingStrategy',
      component:PricingStrategy,
      meta: {
        showSlideBar: true
      },
    },
    { //任务列表
      path:'/TaskList',
      name:'TaskList',
      component:TaskList,
      meta: {
        showSlideBar: true
      },
    },
    { //营收日报
      path:'/EaringsRecord',
      name:'EaringsRecord',
      component:EarningsRecord,
      meta: {
        showSlideBar: true
      },
    },
  ]
})



