<!--  -->
<template>
  <div class="table">
    <div id="wrap">
      <div class="title">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>用户管理</el-breadcrumb-item>
          <el-breadcrumb-item>包裹列表</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="main">
        <el-row :span="24">
          <el-col :span="8">
            <el-form :inline="true" :model="formInline" class="demo-form-inline" align="right">
              <el-row class="timeExcel">
                <span>支付时间</span>&nbsp;&nbsp;
                <el-date-picker
                  v-model="value1"
                  type="daterange"
                  class="time"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </el-row>

              <el-row>
                <el-form-item label="包裹状态" class="state">
                  <el-select v-model="formInline.region" placeholder="全部" style="width:190px">
                    <el-option label="区域一" value="shanghai"></el-option>
                    <el-option label="区域二" value="beijing"></el-option>
                  </el-select>
                </el-form-item>
              </el-row>

              <el-row>
                <el-form :inline="true" :model="formInline" class="demo-form-inline" align="right">
                  <el-form-item label="京东/淘宝等订单号">
                    <el-input v-model="formInline.user" placeholder="请输入内容" style="width:190px"></el-input>
                  </el-form-item>
                </el-form>
              </el-row>
            </el-form>
          </el-col>
          <el-col :span="8">
            <el-form :inline="true" :model="formInline" class="demo-form-inline" align="right">
              <el-form-item label="用户账号">
                <el-input v-model="formInline.user" placeholder="请输入内容" style="width:190px"></el-input>
              </el-form-item>
              <el-form-item label="包裹号">
                <el-input v-model="formInline.user" placeholder="请输入内容" style="width:190px"></el-input>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="8" class="last">
            <el-form :inline="true" :model="formInline" class="demo-form-inline" align="right">
              <el-form-item label="平台任务号">
                <el-input v-model="formInline.user" placeholder="请输入内容" style="width:190px"></el-input>
              </el-form-item>
              <el-form-item label="快递单号">
                <el-input v-model="formInline.user" placeholder="请输入内容" style="width:190px"></el-input>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
        <div class="btnWrap">
          <el-button style="width:100px">搜索</el-button>
        </div>
      </div>
    </div>

    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="name" label="平台任务号" align="center"></el-table-column>
      <el-table-column prop="name" label="包裹号" align="center"></el-table-column>
      <el-table-column prop="name" label="用户账号" align="center"></el-table-column>
      <el-table-column prop="name" label="账号备注" align="center"></el-table-column>
      <el-table-column prop="name" label="金额" align="center"></el-table-column>
      <el-table-column prop="name" label="第三方订单" align="center"></el-table-column>
      <el-table-column prop="name" label="包裹状态" align="center"></el-table-column>
      <el-table-column prop="name" label="快递单号" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      formInline: {
        user: "",
        region: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        },
        {
          date: "2016-05-02",
          name: "王小虎"
        }
      ],
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",
      value2: ""
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='stylus' scoped>
.table {
  border: 1px solid #ccc;
  height: 40px;
  line-height: 40px;
  background: #eee;

  .el-table {
    border: 1px solid #ccc;
    margin-top: 15px;
  }

  #wrap {
    // border: 1px solid #cccccc;

    .main {
      background: #fff;
      padding: 15px;
      border: 1px solid #ccc;
      .state {
        margin-top: 24px;
      }

      .time {
        width: 230px;
      }

      .timeExcel {
        position: relative;
        top: 0;
        left: -10px;
      }

      .btnWrap {
        text-align: center;
      }
    }
  }
}

.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}
</style>


