<!--  -->
<template>
  <div class="page">
    <div class="title">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>提现</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="body">
      <div class="applyFor">
        <div class="form">
          <div class="forTile">可提现金额0元</div>
          <h3>申请提现</h3>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="银行">
              <el-input placeholder="请输入" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="开户地">
              <el-input placeholder="请输入开户支行" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <!-- <el-form-item label="开户省市">
              <el-input class="region" v-model="form.name" label-width="80px" placeholder="请输入省"></el-input> -
              <el-input class="region" v-model="form.name" label-width="80px" placeholder="请输入市"></el-input>
            </el-form-item>-->

            <el-row style="margin-bottom:20px">
              &nbsp;&nbsp;&nbsp;<span style="font-size:14px;color:#606266">开户省市</span>&nbsp;
              <el-select v-model="value5" multiple placeholder="请选择省">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>

              <el-select
                v-model="value11"
                multiple
                collapse-tags
                style="margin-left: 20px;"
                placeholder="请选择市"
                width=100
              >
                <el-option
                  v-for="item in option"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  
                ></el-option>
              </el-select>
            </el-row>

            <el-form-item label="银行卡号">
              <el-input placeholder="请输入卡号" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input placeholder="请输入" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
      
            <el-form-item class="active" >
              <el-row style="margin-left:-79px">
                <span>
                <span style="color:red">*</span>
                <span style="color:#606266">申请金额</span>&nbsp;&nbsp;
              </span>
              <el-input v-model="form.name" label-width="80px"></el-input>
              </el-row>
              
            </el-form-item>
            <el-form-item>
              <el-button  @click="onSubmit">发起申请</el-button>
            </el-form-item>
            <div class="text">备注：提取金额为上一个自然月内金额,时间每月的10号且仅限一次</div>
          </el-form>
        </div>
      </div>
      <div class="record">
        <h3 class="recordTitle">提现记录</h3>
        <div class="table">
          <el-table
            :header-cell-style="{background:'#000',color:'#fff'}"
            :data="tableData"
            style="width: 100%"
          >
            <el-table-column prop="date" label="提现银行" align="center"></el-table-column>
            <el-table-column prop="name" label="银行卡号" align="center"></el-table-column>
            <el-table-column prop="address" label="姓名" align="center"></el-table-column>
            <el-table-column prop="address" label="金额" align="center"></el-table-column>
            <el-table-column prop="address" label="状态" align="center"></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ],
      options: [{
          value: '选项1',
          label: '河南'
        }, {
          value: '选项2',
          label: '河北'
        }, {
          value: '选项3',
          label: '湖南'
        }, {
          value: '选项4',
          label: '湖北'
        }, {
          value: '选项5',
          label: '浙江'
        }],
        value5: [],
        value11: [],
        option: [{
          value: '选项1',
          label: '北京'
        }, {
          value: '选项2',
          label: '上海'
        }, {
          value: '选项3',
          label: '深圳'
        }, {
          value: '选项4',
          label: '广州'
        }, {
          value: '选项5',
          label: '杭州'
        }],
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang='stylus' scoped>
.page {
  border: 1px solid #ccc;
  height: 800px;

  .body {
    position: relative;

    .record {
      position: absolute;
      right: 20px;
      top: 2%;
      width: 50%;

      .recordTitle {
        text-align: center;
      }

      .table {
        margin-top: 20px;
      }
    }
  }

  .title {
    height: 40px;
    line-height: 40px;
    background: #eee;
    border-bottom: 1px solid #ccc;
  }

  .form {
    width: 45%;
    padding-left: 10px;
    border-right: 1px solid #ccc;
    margin-top: 20px;

    .forTile {
      width: 95%;
      padding-left: 10px;
      border-bottom: 1px solid #ccc;
      height: 40px;
      line-height: 40px;
    }

    h3 {
      text-align: center;
      height: 50px;
      line-height: 50px;
    }

    .el-input {
      width: 350px;
    }

    .region {
      width: 168px;
    }

    .text {
      text-align: center;
      color: #ccc;
    }
  }
}

.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;
}


.el-select{
  width:163px;
}

.el-form-item__content{
  margin-left:0;
}
</style>


